













































import { Vue, Component, Prop } from 'vue-property-decorator';
// import { IconFontGridData } from '@/types/components/iconFontGrid.interface';
// import IconFont from '@/components/IconFont.vue';

@Component({
  components: {
    // IconFont
  }

  // data(): IconFontGridData {
  //   return {
  //     msg: 'XXX component'
  //   };
  // }
})
export default class IconFontGrid extends Vue {
  // msg!: string;

  @Prop({
    type: Object,
    default() {
      return {
        grid: '16gl',
        mainClass: 'Basic',
        name: 'home7',
        tag: '家,首页',
        fontSize: '1rem',
        enableHover: true
      };
    }
  })
  readonly icon!: object;
  @Prop({
    type: Number,
    default: 16
  })
  readonly grid!: number;
  @Prop({
    type: Number,
    default: 64
  })
  readonly size!: number;
  @Prop(String) readonly bigBgColor!: string;
  @Prop(String) readonly smallBgColor!: string;
  @Prop(String) readonly borderColor!: string;
  @Prop(String) readonly borderColorDark!: string;
  @Prop(Boolean) readonly enableIcon!: boolean;

  get icon2() {
    return Object.assign({}, this.icon, { fontSize: `${this.size / 16}rem` });
  }
  get bigGridStyle() {
    return {
      width: `${this.size}px`,
      height: `${this.size}px`,
      backgroundColor: this.bigBgColor
    };
  }
  get smallGridStyle() {
    return {
      width: `${this.size / this.grid}px`,
      height: `${this.size / this.grid}px`,
      backgroundColor: this.smallBgColor,
      borderColor: this.borderColor
    };
  }
  get smallDarkGridStyle() {
    return {
      width: `${this.size / this.grid}px`,
      height: `${this.size / this.grid}px`,
      backgroundColor: this.smallBgColor,
      borderColor: this.borderColorDark
    };
  }
}
